<template>
  <transition name="slide">
    <music-list :title="title" :singer-img="bgImage" :songs="songs"></music-list>
  </transition>
</template>

<script type="text/ecmascript-6">
  import musicList from 'components/music-list/music-list'
  import {mapGetters} from 'vuex'
  import {getCdList} from 'api/recommend'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  export default{
    components:{
      musicList
    },
    computed:{
      title(){
        return this.disc.dissname
      },
      bgImage(){
        return this.disc.imgurl
      },
      ...mapGetters([
        'disc'
      ])
    },
    data(){
      return {
        songs:[]
      }
    },
    created(){
      setTimeout(() => {
        this._getCdlist()
      },20)
    },
    methods:{
      _getCdlist(){
        if(!this.disc.dissid){
          this.$router.push({
            path:'/recommend'
          })
        }
        getCdList(this.disc.dissid).then((res) => {
          if(res.code === ERR_OK){
            this.songs = this._normalizeCdList(res.cdlist[0].songlist)
          }
        })
      },
      _normalizeCdList(list){
        let ret = []
        list.forEach((item) => {
          if(item.songid && item.albummid){
            ret.push(createSong(item))
          }
        })
        return ret
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .slide-enter-active, .slide-leave-active
    transition: all 0.3s

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
</style>



