<template>
  <div class="singer" ref="singer">
    <list-view :data="singerList" @selectItem="selectSinger" ref="list"></list-view>
    <router-view></router-view>
  </div>
</template>

<script>
  import ListView from 'base/listview/listview'
  import getSingerList from 'api/singer'
  import Singer from 'common/js/singer'
  import { mapMutations } from 'vuex'
  import {playlistMixin} from 'common/js/mixin'
  const HOT_NAME = ''
  const HOT_SINGER_LEN = 10
  export default {
    mixins:[playlistMixin],
    components:{
      ListView
    },
    data(){
      return{
        singerList:[]
      }
    },
    created(){
      this._getSingerList();
    },
    methods:{
      handlerList(playlist){
        const bottom = playlist.length>0 ? '60px' : 0
        this.$refs.singer.style.bottom = bottom
        this.$refs.list.refresh()
      },
      selectSinger(singer){
        this.$router.push({
          path:`/singer/${singer.id}`
        })
        this.setSinger(singer)
      },
      _getSingerList(){
        getSingerList().then((res) => {
          this.singerList = this._normaLizeList(res.data.list);
        })
      },
      _normaLizeList(list){
        let itemList = {
          hot:{
            title: HOT_NAME,
            items:[]
          }
        }
        list.forEach((item,index) => {
          if(index < 10){
            itemList.hot.items.push(new Singer({
              id:item.Fsinger_mid,
              name:item.Fsinger_name
            }))
          }
          const key = item.Findex;
          if(!itemList[key]){
            itemList[key] = {
              title : key,
              items:[]
            }
          }
          itemList[key].items.push(new Singer({
            id:item.Fsinger_mid,
            name:item.Fsinger_name
          }))
        })
        let hot = []
        let ret = []
        for(let key in itemList){
          let val = itemList[key]
          if(val.title.match(/[a-zA-Z]/)){
            ret.push(val)
          }else if(val.title == HOT_NAME) {
            hot.push(val)
          }
        }
        ret.sort((a,b) => {
          return a.title.charCodeAt(0) - b.title.charCodeAt(0)
        })
        return hot.concat(ret)
      },
      ...mapMutations({
        setSinger: 'SET_SINGER'
      })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .singer
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
</style>



