<template>
  <div class="player" v-show="playlist.length > 0">
    <transition name="normal"
                @enter="enter"
                @after-enter="afterEnter"
                @leave="leave"
                @after-leave="afterLeave"
    >
      <div class="normal-player" v-show="fullScreen">
      <div class="background">
        <img width="100%" height="100%" :src="currentSong.image">
      </div>
      <div class="top">
        <div class="back" @click="back">
          <i class="icon-back"></i>
        </div>
        <h1 class="title" v-html="currentSong.name"></h1>
        <h2 class="subtitle"  v-html="currentSong.singer"></h2>
      </div>
      <div class="middle"
        @touchstart.prevent="onMiddleTouchStart"
        @touchmove.prevent="onMiddleTouchMove"
        @touchend="onMiddleTouchEnd"
      >
        <div class="middle-l" ref="middleL">
          <div class="cd-wrapper" ref="cdWrapper">
            <div class="cd" :class="cdCls">
              <img class="image" :src="currentSong.image">
            </div>
          </div>
          <div class="playing-lyric-wrapper">
            <div class="playing-lyric">{{playingLyric}}</div>
          </div>
        </div>
        <scroll class="middle-r" ref="lyricList">
          <div class="lyric-wrapper">
            <div v-if="currentLyric">
              <p ref="lyricLine"
                 class="text"
                 :class="{'current':index===currentLineNum}"
                 v-for="(line,index) in currentLyric.lines">{{line.txt}}</p>
            </div>
          </div>
        </scroll>
      </div>
      <div class="bottom">
        <div class="dot-wrapper">
          <span class="dot" :class="{'active':currentClass=='cd'}"></span>
          <span class="dot" :class="{'active':currentClass=='lyric'}"></span>
        </div>
        <div class="progress-wrapper">
          <span class="time time-l">{{format(currentTime)}}</span>
          <div class="progress-bar-wrapper">
            <progress-bar :percent="percent" @percentChange="percentChange"></progress-bar>
          </div>
          <span class="time time-r">{{format(currentSong.duration)}}</span>
        </div>
        <div class="operators">
          <div class="icon i-left" @click="changeMode">
            <i :class="stateIcon"></i>
          </div>
          <div class="icon i-left" @click="prev" :class="disableCls">
            <i class="icon-prev"></i>
          </div>
          <div class="icon i-center">
            <i :class="playIcon" @click="togglePlaying"></i>
          </div>
          <div class="icon i-right" @click="next" :class="disableCls">
            <i class="icon-next"></i>
          </div>
          <div class="icon i-right">
            <i class="icon icon-not-favorite"></i>
          </div>
        </div>
      </div>
    </div>
    </transition>
    <transition name="mini">
      <div class="mini-player" v-show="!fullScreen" @click="open">
        <div class="icon">
          <img :class="cdCls" width="40" height="40" :src="currentSong.image">
        </div>
        <div class="text">
          <h2 class="name" v-html="currentSong.name"></h2>
          <p class="desc" v-html="currentSong.singer"></p>
        </div>
        <div class="control">
          <progress-circle :percent="percent" :radius="32">
            <i @click.stop="togglePlaying" class="icon-mini" :class="miniIcon"></i>
          </progress-circle>
        </div>
        <div class="control">
          <i class="icon-playlist"></i>
        </div>
      </div>
    </transition>
    <audio :src="currentSong.url" ref="audio" @canplay="ready" @error="error" @timeupdate="updateTime" @ended="end"></audio>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapGetters,mapMutations} from 'vuex'
  import {prefixStyle} from 'common/js/dom'
  import animations from 'create-keyframe-animation'
  import progressBar from 'base/progress-bar/progress-bar'
  import progressCircle from 'base/progress-circle/progress-circle'
  import {playMode} from 'common/js/config'
  import {shuffle} from 'common/js/util'
  import Lyric from 'lyric-parser'
  import scroll from 'base/scroll/scroll'
  let transition = prefixStyle('transition')
  let transform = prefixStyle('transform')
  let transitionDuration = prefixStyle('transitionDuration')
  export default {
    data(){
      return{
        songReady:false,
        currentTime:0,
        currentLyric:null,
        currentLineNum:0,
        currentClass:'cd',
        playingLyric:''
      }
    },
    computed:{
      playIcon(){
        return this.playing ? 'icon-pause' : 'icon-play'
      },
      miniIcon(){
        return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
      },
      stateIcon(){
        return this.mode === playMode.sequence ? 'icon-sequence' : this.mode === playMode.loop ? 'icon-loop' :'icon-random'
      },
      cdCls() {
        return this.playing ? 'play' : 'play pause'
      },
      disableCls(){
        return this.songReady ? '' : 'disable'
      },
      percent(){
        return this.currentTime / this.currentSong.duration
      },
      ...mapGetters([
        'playlist',
        'fullScreen',
        'currentSong',
        'playing',
        'currentIndex',
        'mode',
        'sequenceList'
      ])
    },
    mounted(){
      setTimeout(() => {

        console.log(this.currentSong);
      },50)
    },
    created(){
      this.touch={}
    },
    methods:{
      back(){
        this.setFullScreen(false)
      },
      open(){
        this.setFullScreen(true)
      },
      changeMode(){
        let mode = (this.mode+1) % 3
        this.setPlayMode(mode)
        let list = null
        if(mode === playMode.random){
          list = shuffle(this.sequenceList)
        }else{
          list = this.sequenceList
        }
        this.resetCurrentIndex(list)
        this.setPlayList(list)
      },
      resetCurrentIndex(list){
        let index = list.findIndex((item) => {
          return item.id === this.currentSong.id
        })
        this.setCurrentIndex(index)
      },
      enter(el,done){
        let {x,y,scale} = this._getPosAndScale()
        let animation={
          0:{
            transform:`translate3d(${x}px,${y}px,0) scale(${scale})`
          },
          60:{
            transform:`translate3d(0,0,0) scale(1.1)`
          },
          100:{
            transform:`translate3d(0,0,0) scale(1)`
          }
        }
        animations.registerAnimation({
          name:"move",
          animation,
          presets:{
            duration:400,
            easing:'linear'
          }
        })
        animations.runAnimation(this.$refs.cdWrapper,'move',done)
      },
      afterEnter(){
        animations.unregisterAnimation('move')
        this.$refs.cdWrapper.style[transition] = ''
      },
      leave(el,done){
        let {x,y,scale} = this._getPosAndScale()
        this.$refs.cdWrapper.style[transition]=`all .4s`
        this.$refs.cdWrapper.style[transform]=`translate3d(${x}px,${y}px,0) scale(${scale})`
        this.$refs.cdWrapper.addEventListener("transitionend",done)
      },
      afterLeave(){
        this.$refs.cdWrapper.style[transition]=''
        this.$refs.cdWrapper.style[transform]=''
      },
      togglePlaying(){
        if(!this.songReady){
          return
        }
        this.setPlayingState(!this.playing)
        if(this.currentLyric){
          this.currentLyric.togglePlay()
        }
      },
      next(){
        if(!this.songReady){
          return
        }
        let index = this.currentIndex + 1;
        if(index === this.playlist.length){
          index = 0
        }
        this.setCurrentIndex(index)
        if(!this.playing){
          this.togglePlaying()
        }
        this.songReady = false
      },
      prev(){
        if(!this.songReady){
          return
        }
        let index = this.currentIndex - 1;
        if(index === -1){
          index = this.playlist.length -1
        }
        this.setCurrentIndex(index)
        if(!this.playing){
          this.togglePlaying()
        }
        this.songReady = false
      },
      ready(){
        this.songReady = true
      },
      error(){
        this.songReady = true
      },
      end(){
        if(this.mode === playMode.loop){
          this.loop()
        }else{
          this.next()
        }
      },
      loop(){
        this.$refs.audio.currentTime = 0
        this.$refs.audio.play()
        if(this.currentLyric){
          this.currentLyric.seek(0)
        }
      },
      updateTime(e){
        this.currentTime = e.target.currentTime
      },
      format(interval){
        let time = interval | 0
        let minutes = time / 60 | 0
        let seconds = this._pad(time % 60)
        return `${minutes}:${seconds}`
      },
      percentChange(percent){
        const currentTime = percent * this.currentSong.duration
        this.currentTime = currentTime
        this.$refs.audio.currentTime = this.currentTime
        if(!this.playing){
          this.togglePlaying();
        }
        if(this.currentLyric){
          this.currentLyric.seek(currentTime * 1000)
        }
      },
      handler({lineNum, txt}){
        this.currentLineNum = lineNum
        if(lineNum <= 5){
          this.$refs.lyricList.scrollTo(0,0,0)
        }else{
          this.$refs.lyricList.scrollToElement(this.$refs.lyricLine[lineNum-5],300)
        }
        this.playingLyric = txt
      },
      getLyric(){
        this.currentSong.getLyric().then((lyric) => {
          if(this.currentSong.lyric !== lyric){
            return
          }
          this.currentLyric = new Lyric(lyric,this.handler)
          if(this.playing){
            this.currentLyric.play()
          }
        }).catch((err) => {
          this.currentLyric = null
          this.playingLyric = ''
          this.currentLineNum = 0
        })
      },
      onMiddleTouchStart(e){
        this.initiated = true
        let touch = e.touches[0]
        this.touch.startX = touch.pageX
        this.touch.startY = touch.pageY
      },
      onMiddleTouchMove(e){
        if(!this.initiated){
          return
        }
        let touch = e.touches[0]
        let deltaX = touch.pageX - this.touch.startX
        let deltaY = touch.pageY - this.touch.startY
        if(Math.abs(deltaX) < Math.abs(deltaY)){
          return
        }
        let left = this.currentClass === 'cd' ? 0 : -window.innerWidth
        let offsetWidth = left + deltaX
        let time = 300
        this.touch.percent = offsetWidth / -window.innerWidth
        let opacity = 1-this.touch.percent
        this.$refs.lyricList.$el.style[transform] =  `translate3d(${offsetWidth}px,0,0)`
        this.$refs.lyricList.$el.style[transitionDuration] =  `${time}ms`
        this.$refs.middleL.style.opacity = opacity
        this.$refs.middleL.style[transitionDuration] =  `${time}ms`
      },
      onMiddleTouchEnd(){
        let offsetWidth
        let opacity
        if(this.currentClass === 'cd'){
          if(this.touch.percent > 0.1){
            offsetWidth = -window.innerWidth
            this.currentClass = 'lyric'
            opacity = 0
          }else{
            offsetWidth = 0
            opacity = 1
          }
        }else {
          if(this.touch.percent < 0.9){
            offsetWidth = 0
            this.currentClass = 'cd'
            opacity = 1
          }else{
            offsetWidth = -window.innerWidth
            opacity = 0
          }
        }
        const time = 300
        this.$refs.lyricList.$el.style[transform] =  `translate3d(${offsetWidth}px,0,0)`
        this.$refs.lyricList.$el.style[transitionDuration] =  `${time}ms`
        this.$refs.middleL.style.opacity = opacity
        this.$refs.middleL.style[transitionDuration] =  `${time}ms`
        this.initiated = false
      },
      _pad(num,n=2){
        let len = num.toString().length;
        while (len<n) {
          num = '0'+num
          len++
        }
        return num
      },
      _getPosAndScale(){
        const paddingTop = 80
        const paddingLeft = 40
        const paddingBottom = 30
        const oWidth = window.innerWidth*0.8
        const scale = 40/oWidth
        const x= -(oWidth/2-paddingLeft)
        const y = window.innerHeight - paddingTop - oWidth/2 - paddingBottom
        return{
          x,
          y,
          scale
        }
      },
      ...mapMutations({
        setFullScreen:'SET_FULL_SCREEN',
        setPlayingState:'SET_PLAYING_STATE',
        setCurrentIndex:'SET_CURRENT_INDEX',
        setPlayMode: 'SET_PLAY_MODE',
        setPlayList:'SET_PLAYLIST'
      })
    },
    watch:{
      currentSong(newSong,oldSong){
        if(newSong.id === oldSong.id){
          return
        }
        if(this.currentLyric){
          this.currentLyric.stop()
          this.playingLyric = ''
          this.currentTime = 0
          this.currentLineNum = 0
        }
        this.$nextTick(() => {
          this.$refs.audio.play()
          this.getLyric();
        })
      },
      playing(newPlaying){
        let audio = this.$refs.audio;
        this.$nextTick(() => {
          newPlaying ? audio.play() : audio.pause()
        })
      }
    },
    components:{
      progressBar,
      progressCircle,
      scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: $color-background
      .background
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1
        opacity: 0.6
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 0
          left: 6px
          z-index: 50
          .icon-back
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme
            transform: rotate(-90deg)
        .title
          width: 70%
          margin: 0 auto
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-text
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-text
      .middle
        position: fixed
        width: 100%
        top: 80px
        bottom: 170px
        white-space: nowrap
        font-size: 0
        .middle-l
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 80%
          .cd-wrapper
            position: absolute
            left: 10%
            top: 0
            width: 80%
            height: 100%
            .cd
              width: 100%
              height: 100%
              box-sizing: border-box
              border: 10px solid rgba(255, 255, 255, 0.1)
              border-radius: 50%
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                border-radius: 50%

          .playing-lyric-wrapper
            width: 80%
            margin: 30px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color: $color-text-l
        .middle-r
          display: inline-block
          vertical-align: top
          width: 100%
          height: 100%
          overflow: hidden
          .lyric-wrapper
            width: 80%
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: $color-text-l
              font-size: $font-size-medium
              &.current
                color: $color-text
      .bottom
        position: absolute
        bottom: 50px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-text-l
            &.active
              width: 20px
              border-radius: 5px
              background: $color-text-ll
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 10px 0
          .time
            color: $color-text
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme
            &.disable
              color: $color-theme-d
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 40px
          .i-right
            text-align: left
          .icon-favorite
            color: $color-sub-theme
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.4s
        .top, .bottom
          transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: $color-highlight-background
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        padding: 0 10px 0 20px
        img
          border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-text
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-text-d
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .icon-play-mini, .icon-pause-mini, .icon-playlist
          font-size: 30px
          color: $color-theme-d
        .icon-mini
          font-size: 32px
          position: absolute
          left: 0
          top: 0

  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>



