import {mapGetters} from 'vuex'

export const playlistMixin = {
  computed:{
    ...mapGetters([
      'playlist'
    ])
  },
  mounted(){
    this.handlerList(this.playlist)
  },
  activated(){
    this.handlerList(this.playlist)
  },
  watch:{
    playlist(newVal){
      this.handlerList(newVal)
    }
  },
  methods:{
    handlerList(){
      throw new error('component must implement handlePlaylist method')
    }
  }
}



