<template>
  <scroll ref="listview" class="listview" :data="data" :listenScroll="listenScroll" @scroll="scroll" :probeType="probeType">
    <ul>
      <li v-for="group in data" class="list-group" ref="listGroup">
        <h2 class="list-group-title">{{group.title}}</h2>
        <uL>
          <li v-for="item in group.items"  @click="select(item)" class="list-group-item">
            <img class="avatar" v-lazy="item.avatar">
            <span class="name" v-html="item.name"></span>
          </li>
        </uL>
      </li>
    </ul>
    <div class="list-shortcut" @touchstart.stop.prevent="onShortcutTouchStart" @touchmove.stop.prevent="onShortcutTouchMove" @touchend.stop>
      <ul>
        <li class="item" v-for="(item,index) in shortcutList" :data-index="index" :class="{'current':index==currentIndex}">
            {{item}}
        </li>
      </ul>
    </div>
    <div class="list-fixed" ref="fixed" v-show="fixedTitle">
      <div class="fixed-title">{{fixedTitle}} </div>
    </div>
    <div v-if="!data.length" class="loading-container">
       <loading></loading>
    </div>
  </scroll>
</template>

<script type="text/ecmascript-6">
  import scroll from 'base/scroll/scroll'
  import loading from 'base/loading/loading'
  import { getData } from 'common/js/dom'
  const ELE_HEIGHT = 18
  const TOP_HEIGHT = 30
  export default {
    props:{
      data:{
        type : Array,
        default:[]
      }
    },
    data(){
      return{
        currentIndex:0,
        scrollY:-1,
        diff:-1
      }
    },
    created() {
      this.touch={}
      this.probeType = 3
      this.listenScroll = true
      this.listHeight = []
    },
    computed:{
      shortcutList(){
        return this.data.map((item) => {
          return item.title.substr(0,1)
        })
      },
      fixedTitle(){
        if(this.scrollY > 0){
          return
        }
        return this.data[this.currentIndex] ? this.data[this.currentIndex].title : ''
      }
    },
    components:{
      scroll,
      loading
    },
    methods:{
      refresh(){
        this.$refs.listview.refresh();
      },
      select(item){
        this.$emit('selectItem',item)
      },
      scroll(pos){
        this.scrollY = pos.y
      },
      onShortcutTouchStart(e){
        let anchorIndex = getData(e.target,'index');
        let firstTouch = e.touches[0];
        this.touch.y1= firstTouch.pageY;
        this.touch.anchorIndex = anchorIndex;
        this._scrollTo(anchorIndex);
      },
      onShortcutTouchMove(e){
        let endTouch = e.touches[0];
        this.touch.y2 = endTouch.pageY;
        let delta = (this.touch.y2 - this.touch.y1) / ELE_HEIGHT | 0
        let anchorIndex = parseInt(this.touch.anchorIndex) + delta
        this._scrollTo(anchorIndex)
      },
      _scrollTo(index){
        if(!index && index != 0){
          return
        }
        if(index < 0){
          index = 0
        }
        if(index >= this.data.length-1){
          index = this.data.length-1
        }
        this.scrollY = -this.listHeight[index]
        this.$refs.listview.scrollToElement(this.$refs.listGroup[index],0)
      },
      _calculateHeight(){
        let height = 0
        this.listHeight = []
        this.listHeight.push(height)
        let len = this.$refs.listGroup.length;
        for(let i =0;i<len;i++){
          height+=this.$refs.listGroup[i].clientHeight
          this.listHeight.push(height)
        }
      }
    },
    watch:{
      data(){
        setTimeout(() => {
          this._calculateHeight()
        },20)
      },
      scrollY(newY){
        const listHeight = this.listHeight
        if(newY > 0){
          this.currentIndex = 0;
          return
        }

        for(let i=0;i<listHeight.length-1;i++){
          let down = listHeight[i]
          let up = listHeight[i+1]
          if(-newY < up && -newY >= down){
            this.currentIndex = i
            this.diff = up+newY
            return
          }
        }

        if(-newY > listHeight.length-2){
          this.currentIndex = listHeight.length-2
        }
      },
      diff(newVal){
        let offY = (newVal >0 && newVal < TOP_HEIGHT) ? newVal - TOP_HEIGHT : 0;
        if(offY == this.offY){
          return
        }
        this.offY = offY
        this.$refs.fixed.style.transform = `translate3d(0,${this.offY}px,0)`
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .listview
    position: relative
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    .list-group
      padding-bottom: 30px
      .list-group-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
      .list-group-item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-l
          font-size: $font-size-medium
    .list-shortcut
      position: absolute
      z-index: 30
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 20px 0
      border-radius: 10px
      text-align: center
      background: $color-background-d
      font-family: Helvetica
      .item
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme
    .list-fixed
      position: absolute
      top: 0
      left: 0
      width: 100%
      .fixed-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
    .loading-container
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



